.title{
    text-align: center;
    position: relative;
    font-size: 40px;
    font-weight: 500;;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-family: 'Ubuntu',sans-serif;
}
.title::before{
    content: "";
    position: absolute;
    width: 180px;
    height: 3px;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: #111;
}
.title::after{
    position: absolute;
    padding: 5px;
    bottom: -12px;
    left: 50%;
    font-size: 20px;
    color: crimson;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: #fff;
}
