
.pro-contain .title::before{
    background: #fff;
}
.pro-contain .title::after{
    background: #111;
    content: "my Projects";
}
.pro-contain {
    margin-top: 5%;
    background-color: black;
    background-image: radial-gradient(circle at center, rgb(44, 42, 42) 0%, #222 10%, black 60%);
	color: white;
    text-align: center;
}
.projects {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.projects .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    font-size: 25px;
    font-weight: 700;
    display: flex;
    align-items:center;
    justify-content:center;
    transition: .9s ease;
    background-color: #0d0d0d;
}
.projects .overlay:hover{
    opacity: 0.9;
}
.pro-contain >.projects > .card1 > .overlay >span{
    font-size: 10px;
}
.card1 {
    animation: fadeIn;
    background-color: rgb(255, 255, 255);
    color: rgb(255, 255, 255);
    height: 10rem;
    overflow: hidden;
    position: relative;
    border: solid black;
    border-radius: 15px;
    height: 250px;
    width: 400px;
    margin: 20px;
}
.card1:hover{
    transform: scale(1.1);
    transition: 2000ms;
}
#pro1{
    background-image: url(./images/pro1img.png);
    background-size: cover;
}#pro2{
    background-image: url(./images/pro2img.png);
    background-size: cover;
}#pro3{
    background-image: url(./images/pro3img.png);
    background-size: cover;
}

@media  (max-width: 400px){
    .card1{
    height: 180px;
    width: 280px;}

}